.projectpage-title {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 500;
  color: rgb(0, 0, 0);
}
.projectpage-title.about {
  color: rgba(255, 255, 255, 0.7);
  transition: 0.5s;
}

.projectpage-title::before {
  width: 50%;
  position: absolute;
  content: "";
  left: 50%;
  height: 1px;
  top: 30px;
  background-color: rgb(17, 17, 17);
  transform: translateX(-50%);
  transition: 0.5s;
}

.projectpage-title.about::before {
  background-color: rgba(255, 255, 255, 0.7);
}
.projectpage-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 1s ease-in-out;
  display: flex;
  overflow: hidden;
}
.projectpage-box {
  position: absolute;
  top: 0;
  width: 100%;
  height: 300%;
  transition: 1s top;
}
.projectpage-project {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 1s opacity;
}

.projectpage-buttonBox {
  position: absolute;
  width: 80px;
  height: 50px;
  bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.projectpage-button {
  width: 15px;
  height: 15px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  transition: 0.2s;
}

.projectpage-buttonBox.contact .projectpage-button {
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.projectpage-button.active {
  border: 8px solid rgb(0, 0, 0);
  transition: 0.5s;
}
.projectpage-buttonBox.contact .projectpage-button.active {
  border: 8px solid rgb(255, 255, 255);
}

.projectpage-projectNameBox {
  position: absolute;
  width: 250px;
  height: 25px;
  bottom: 60px;
  pointer-events: none;
}

.projectpage-projectNameBox .projectpage-projectNameBox-ul {
  position: relative;
  width: 100%;
  height: 90px;

  left: 0;
  transition: 1s;
}

.projectpage-projectNameBox .projectpage-projectNameBox-list {
  position: relative;
  width: 250px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
  color: #898989;
  font-weight: 500;
  transition: 0.5s;
}

.projectpage-projectNameBox .projectpage-projectNameBox-list span {
  position: relative;
  left: 5px;

  color: rgba(0, 0, 0, 0.7);
  transition: 0.5s;
}

.projectpage-projectNameBox.contact .projectpage-projectNameBox-list span {
  color: rgba(255, 255, 255, 1);
}
