.diary-container {
  position: relative;
  width: 100%;
  height: 100%;
}
/* d598c2 */

.diary-wrapper {
  position: absolute;
  width: 300px;
  height: 430px;

  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);

  overflow: hidden;
  filter: brightness(0.95);
}

.diary-wrapper.left {
  left: calc(50% - 160px);
  background-color: #a47bc4;
}
.diary-wrapper.right {
  left: calc(50% + 160px);
  background-color: #50aeb6;
}
.diary-wrapper::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-image: url(data:image/png;base64,);
  opacity: 0.3;
  pointer-events: none;
}

.diary-wrapper.left .diary-light {
  position: absolute;
  width: 200px;
  height: 0px;
  left: 10%;
  top: 40%;
  transform: translateX(-50%);
  border-radius: 50%;
  box-shadow: 0px 0px 60px 50px #d598c2;
  opacity: 0.7;
}
.diary-wrapper.right .diary-light {
  position: absolute;
  width: 200px;
  height: 0px;
  left: 10%;
  top: 40%;
  transform: translateX(-50%);
  border-radius: 50%;
  box-shadow: 0px 0px 60px 50px #78cfd7;
  opacity: 0.7;
}

.diary-light::before {
  position: absolute;
  content: "";
  width: 200px;
  height: 0px;
  left: 80%;
  top: 80%;
  transform: translateX(-50%, -50%);
  border-radius: 50%;
  box-shadow: 0px 0px 100px 80px #d598c2;
  opacity: 0.2;
}

.diary-circle {
  position: absolute;
  width: 250px;
  height: 250px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotateX(50deg);
  border-radius: 50%;
}

.diary-text {
  position: absolute;
  width: 100%;
  height: 100%;
}

.diary-text span {
  position: absolute;
  left: 50%;
  width: 22px;
  transform: translate(-50%, -50%);
  font-size: 4px;
  transform-origin: 0 125px;
  text-align: center;
}

.diary-circle-line {
  position: absolute;
  width: 220px;
  height: 220px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotateX(50deg);
  border-radius: 50%;
}

.diary-line {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotate(46deg);
}

.diary-line span {
  width: 0.1px;
  height: 100px;
  background-color: black;
  opacity: 0.2;
}

.diary-line span {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4px;
  transform-origin: 0px 110px;
}

.diary-imgBox {
  position: absolute;
  width: 100%;
  height: 100%;
}

.diary-imgBox img {
  position: absolute;
  left: 64%;
  top: 46%;
  transform: translate(-50%, -50%);
  width: 90%;
}
.diary-wrapper.right .diary-imgBox img {
  position: absolute;
  left: 70%;
  top: 53%;
  transform: translate(-50%, -50%);
  width: 90%;
}

.diary-title {
  position: absolute;
  left: 50%;
  top: 10%;
  width: 100px;
  height: 50px;

  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 2;
}

.diary-title h2 {
  font-size: 15px;
  font-weight: 700;
}
.diary-title p {
  font-size: 5px;
  font-weight: 500;
  color: rgb(56, 56, 56);
}

.diary-exText {
  position: absolute;
  left: 50%;
  top: 80%;
  width: 300px;
  height: 50px;
  transform: translateX(-50%);
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: black;
  z-index: 2;
}

.diary-exText p {
  font-size: 7px;
  font-weight: 500;
}

.diary-linkBtn {
  position: absolute;
  left: 50%;
  top: 95%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  border: none;
  font-size: 10px;
  opacity: 1;
  transition: 0.5s;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
}
.diary-linkBtn::before {
  position: absolute;
  top: 17px;
  content: "";
  width: 0%;
  border-bottom: 1px solid black;
  transition: 0.5s;
}
.diary-linkBtn:hover::before {
  width: 100%;
}

.diary-linkBtn a {
  color: black;
  text-decoration: none;
  transition: 0.5s;
}
