.aboutpage-container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 30px;
  transition: 0.5s;
  transition-delay: 0s;
  opacity: 0;
}

.aboutpage-container.about {
  opacity: 1;
  transition: 3s;
  transition-delay: 0.5s;
}
