.aboutpage-box {
  position: absolute;
  top: 0%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  transition: 0.5s;
}

.aboutpage-btnBox {
  position: absolute;
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  width: 250px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.aboutpage-btnBox button {
  border: none;
  background-color: transparent;
  font-size: 10px;
  transition: 0.5s;
  color: white;
  text-transform: uppercase;
}

.aboutpage-btnBox button span {
  transition: 0.5s;
}

.aboutpage-btnBox button:hover span {
  color: rgb(251, 251, 251);
}
.aboutpage-btnBox.black button:hover span {
  color: rgb(0, 0, 0);
}

.skill-btn {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  border: none;
  color: white;
}

.skill-btn.front {
  top: 40%;
}
.skill-btn.back {
  left: 40%;
  top: 55%;
}
